import React from 'react'
import { graphql } from "gatsby"
import Layout from "../components/layout"
import Projects from '../components/Projects.js'
import backIcon from '../images/close.png'
import AniLink from "gatsby-plugin-transition-link/AniLink"

export default class ProgrammingPage extends React.Component {
    render() {
        return <Layout>
            <div style={{ 
                display: 'flex', 
                alignItems: 'center', 
                marginTop: 24, 
                marginBottom: 24 
            }}>
                    <AniLink 
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                        to="/" 
                        replace 
                        cover 
                        bg="#f5587b" 
                        direction="right"
                    >
                        <img style={{ margin: 0 }} src={ backIcon } alt="" />
                    </AniLink>
                    <h2 style={{ marginLeft: 24 }}>Programming Projects</h2>
                </div>
                <Projects projects={ this.props.data.allProjectsCsv.edges }/>
            </Layout>
    }
}
export const query = graphql`

   fragment ProjectFragment on ProjectsCsv {
        badge
        badge_link
        id
        name
        year
        description
        category
        platform
        language
        occassion
        link
        video
   } 

    query {
        allProjectsCsv(sort: { fields: [year], order: DESC }) {
            edges {
                node {
                    ...ProjectFragment
                }
            }
        }
    }
`
